@import url(https://fonts.googleapis.com/css?family=Indie+Flower|Lato:100i);
:root {

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
    background: none;
    border: 0;
    color: inherit;
    /* cursor: default; */
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    -webkit-appearance: button; /* for input */
    -webkit-user-select: none; /* for button */
       -moz-user-select: none;
        -ms-user-select: none;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(167, 194, 98);
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gameBoard {
  display: flex;
  flex: 0 0 1;
  flex-wrap: wrap;
  width: 100vw;
  height: 100vw;
  box-shadow: 0 2px 50px rgba(100, 100, 100, .5);
  background-image: -webkit-linear-gradient(top left, rgba(202, 202, 202, 0.4), rgba(255,255,255,.2)), url(/winnie-hunt/static/media/grass2.3df533f9.jpg);
  background-image: linear-gradient(to bottom right, rgba(202, 202, 202, 0.4), rgba(255,255,255,.2)), url(/winnie-hunt/static/media/grass2.3df533f9.jpg);

  background-size: cover;
  background-position: center;
}
.gameCell {
  display: flex;
  width: 10vw;
  height: 10vw;
  flex: 0 0 10vw;
  border: none;
  /* outline: 1px solid #ccc; */
}


