           url(/winnie-hunt/static/media/Flaticon.72bd4c6e.woff) format("woff"),
           url(/winnie-hunt/static/media/Flaticon.3bac9792.ttf) format("truetype"),
           url(/winnie-hunt/static/media/Flaticon.90069d82.svg#Flaticon) format("svg");
      font-weight: normal;
      font-style: normal;
    }
    
    @media screen and (-webkit-min-device-pixel-ratio:0) {
      @font-face {
        font-family: "Flaticon";
        src: url(/winnie-hunt/static/media/Flaticon.90069d82.svg#Flaticon) format("svg");
      }
    }
    
    [class^="flaticon-"]:before, [class*=" flaticon-"]:before,
    [class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
      font-family: Flaticon;
      font-size: inherit;
    font-style: normal;
    margin: auto;
    }
    
    .flaticon-paw-print:before { content: "\F100"; }
    .flaticon-pug:before { content: "\F101"; }


.winnie {
  text-align: center;
  font-size: 10vw;
  margin-top: 0px;
  color: #222;
  background-image: -webkit-radial-gradient(center,  ellipse,  rgb(255, 219, 219) 40%, rgb(255, 117, 117) 40%, transparent 50%);
  background-image: radial-gradient(ellipse at center,  rgb(255, 219, 219) 40%, rgb(255, 117, 117) 40%, transparent 50%);
  -webkit-transition: all .6s;
  transition: all .6s;
  position: relative;
}

.paw{
text-align: center;
color: #444;
-webkit-transition: all .6s;
transition: all .6s;
position: relative;
}

.wonton {
  
  position: relative;
}

img {
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: -1;
}

.controls {
  display: flex;
  background-color: rgb(167, 194, 98);
  width: 100vw;
  justify-content: stretch;
  justify-items: stretch;
  flex: 1 1 30vw;
  ;
}

.controlButton {
  display: flex;
  flex: 1 1 30vw;
  text-align: center;
  justify-content: center;
  border: 1px solid #ddd;
  font-size: 30px;
  font-weight: 700;
  color: #444;
  background-color: #ddd;
  -webkit-transition: all 2s ease;
  transition: all 2s ease;
  border: 1px solid #aaa;
  border-radius: 10px;
  margin: 10px;
  box-shadow: 0 2px 50px #ccc;
}


.controlButton:active
 {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  box-shadow: 0 0 40px #ccc;
  background-color: rgb(204, 92, 111);
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
 
}



.centerControls{
  display: flex;
  flex: 1 1 30vw;
  flex-wrap: wrap;
}

.headerfooter {
  height: calc((100vh - 100vw)/2);
  margin: auto;
}

.startButton {
  font-size: 25px;
  font-weight: 100;
  color: white;
  width: 90vw;
  height: 15vw;
  margin: 5vw;
  border-radius: 3px;
  background-image: -webkit-linear-gradient(top left, rgb(167, 194, 98) 0%,  rgb(105, 115, 56) 65%, rgb(120, 128, 71) 100%);
  background-image: linear-gradient(to bottom right, rgb(167, 194, 98) 0%,  rgb(105, 115, 56) 65%, rgb(120, 128, 71) 100%);
  padding: 10px;
  box-shadow: 0 2px 50px #777;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  
}

.startButton:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  box-shadow: 0 0 40px #777;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

.headerText {
  display: flex;
  align-items: center;
  font-family: 'Lato', cursive;
  font-size: 6vw;
  justify-content: center;
  color: white;
  margin: auto;
  height: calc(((100vh - 100vw)/2) - 25vw);
}

.splash {
  padding: 10vh 0;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  -webkit-transform: translateY(-100vh);
          transform: translateY(-100vh);
  left: 0px;
  width: 100vw;
  height: 80vh;
  background-image: -webkit-linear-gradient(top left,#ddd 0%, rgb(231, 213, 216) 50%, #ddd 100%);
  background-image: linear-gradient(to bottom right,#ddd 0%, rgb(231, 213, 216) 50%, #ddd 100%);
  z-index: 100;
  -webkit-transition: all .5s ease-in;
  transition: all .5s ease-in;
}

.slideDown {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all 1s ease-out;
  transition: all 1s ease-out;
}



h1 {
  font-size: 15vw;
  margin: 0 auto;
  font-family: 'Lato', sans-serif;
  color: white;
}

h2 {
  margin: 0 auto;
  font-size: 10vw;
  font-family: 'Indie Flower', cursive;
  color: #888;
}
h4{
  display: flex;
  width: 100vw;
  margin: 10vw auto;
  text-align: center;
  justify-content: center;
  flex: 0 0 auto;
  font-size: 14vw;
  color: teal;
}
h5{
  display: flex;
  width: 100vw;
  margin: 0 auto;
  text-align: center;
  flex: 0 0 auto;
  font-size: 11vw;
  color: #555;

}

.splashText {
  padding: 5vw;
  text-align: center;
  display: flex;
  font-family: 'Lato', sans-serif;
  font-size: 10vw;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  flex: 1 1 auto;
  flex-wrap: wrap;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

